                        </p>
                        <p>
                          <v-icon color="success">
                            mdi-check-bold
                          </v-icon>
                          Fonoaudiología
                        </p>
                        <p>
                          <v-icon color="success">
                            mdi-check-bold
                          </v-icon>
                          Terapia Ocupacional
                        </p> -->
