      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
    },
    data () {
      return {
        charged: false,
      }
    },
    computed: {
      ...mapState('web', ['previsionesConvenios']),
    },
    watch: {
      previsionesConvenios () {
        this.charged = !!this.previsionesConvenios
      },
    },
    created () {
      this.getPrevisionesConvenios()
    // this.print()
    },
    methods: {
      ...mapActions('web', ['getPrevisionesConvenios']),
      print () {
        console.log(this.previsionesConvenios)
      },
    },
  }
</script>
